a:hover {
	text-decoration: none;
}

img {
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
}


img:hover {
	-webkit-transform: scale(1.05);
	    -ms-transform: scale(1.05);
	        transform: scale(1.05);
}

.displayCard {
	padding: 10px 0px 10px 15px;
	border: 1px solid #DFDFDF;
	cursor: pointer;
	overflow: auto;
}



